@use '../base/variables' as v;
@use '../base/mixins' as m;

.citas{
    list-style: none;
    padding: 0;
    
    li{
        border-top: 1px solid v.$gris;
        
        p{
            font-weight: v.$bold;
            color: v.$azul;
        }
        span{
            font-weight: v.$regular;
            color: v.$blanco;
        }
    }
}

.servicios{
    @extend .citas;
}

.tab-servicio{
    background-color: v.$azul;
    padding: 0 2rem;
    border-radius: 7px;
    width: 100%;
    transition-property: background-color;
    transition-duration: .3s;
    &:hover{
        cursor: pointer;
        background-color: darken(v.$azul, 10%);
    }
}

.service-title{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    .titulo, .titulo_flecha{
        display: block;
        color: v.$blanco;
        font-weight: v.$bold;
        font-size: 2.6rem;
    }
    .titulo_flecha{
        transform: rotate(270deg);
    }
    .rotate{
        transform: rotate(90deg);
    }
}

.servicios-detaill{
    visibility: hidden;
    opacity: 0;
    transition: visibility .5s linear, opacity .8s, height .1s linear;
    height: 0;
    padding: 0 2rem;
    &:last-child{
        border-bottom: 3px dashed v.$blanco;
    }
}

.visibility{
    visibility: visible;
    opacity: 1;
    height: auto;
}

.barra-servicios{
    @include m.tablet {
        display: flex;
        justify-content:space-around;
        align-items: center;
    }
}
.back{
    display: flex;
    justify-content: flex-start;

    .btn-back{
        font-weight: bold;
        font-size: 2.6rem;
        color: v.$blanco;
        display: block;
        text-align: left;
        margin: 2rem 0;
        transition: color .3s linear;
        &:hover{
            color: v.$azul;
            border-bottom: 1px v.$azul solid;
        }
    }
}