@use '../base/variables' as v;
@use '../base/mixins' as m;

.contenedor-resumen{

    .contenedor-cita{
        padding: 2rem;
    }
    p{
        font-weight: v.$bold;
        text-align: center;
        @include m.tablet {
            text-align: left;
        }
    }

    span{
        color: v.$azul;
    }
    h3{
        margin: 1.6rem 0;
    }
}

.contenedor-servicio{

    padding: 2rem ;
    border-bottom: 1px solid v.$azul;
    &:last-of-type{
        border-bottom: none;
    }

}