@use '../base/variables' as v;
@use '../base/mixins' as m;

.seccion{
    display: none;
    padding: 5rem 0;
    .text-center{
        text-align: center;
    }

}



.mostrar{
    display: block;
}
.ocultar{
    display: none;
}

.barra{

    display: flex;
    justify-content: space-between;
    align-items: center;
    span{
        font-weight: v.$bold;
    }
    .boton{
        @include m.boton(v.$rojo, v.$blanco, inline-block);
        width: auto;
        margin: 0;
    }
}