@use '../base/mixins' as m;
@use '../base/variables' as v;

.alerta{
    margin: 0 auto .5rem;
    padding: .3rem;
    text-align: center;
    text-transform: uppercase;
    font-weight: v.$bold;
    font-size: 1.8rem;
    &.error{
        color: v.$rojo;
    }
    &.exito{
        color: v.$verde;
    }
}