@use '../base/variables' as v;
@use '../base/mixins' as m;

.boton{
    @include m.boton(v.$azul, v.$blanco, block);
}
.reservar{
    @include m.boton(v.$verde, v.$blanco, block);
}
.editar{
    @include m.boton(v.$amarillo, v.$blanco, block);
}
.boton-eliminar{
    @include m.boton(v.$rojo, v.$blanco, block);
}
.acciones{
    .boton-eliminar{
        @include m.boton(v.$rojo, v.$blanco, block);
        margin: 0;
    }
    .boton-editar{
        @include m.boton(v.$amarillo, v.$blanco, block);
        margin: 0;
    }
}
.boton-citaservicio{
    @include m.boton(v.$azul, v.$blanco, block);
    @include m.tablet{
        margin: 0 0 5rem 0;
    }
}
.boton-servicios{
    @include m.boton(v.$verde, v.$blanco, block);
    @include m.tablet{
        margin: 0 0 5rem 0;
    }
}
.boton-volver{
    @include m.boton(v.$azul, v.$blanco, block);
    margin: 4rem 2rem;
}
.boton-crear{
    @include m.boton(v.$verde, v.$blanco, block);
    margin: 4rem 2rem;
}