@use '../base/variables' as v;
@use '../base/mixins' as m;

.acciones{

    margin-top: 4rem;

    a{
        display: block;
        margin-bottom: v.$separacion / 2;
        text-align: center;
        color: v.$blanco;
        max-width: 100%;
        width: auto;
        &:hover{
            color: darken(v.$blanco , 10%);
        }
    }
    @include m.telefono {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 0;
        margin-bottom: 2rem;
    }
}