@use '../base/variables' as v;
@use '../base/mixins' as m;

.paginacion{
    @include m.telefono{
        display: flex;
        justify-content: space-between;
    }
    button{
        margin: 0 auto 1rem;
        @include m.telefono {
            margin: 0 0 1rem;
        }
    }
    
    .ocultar{
        visibility: hidden;
        display: none;
    }
    @include m.telefono {
        .ocultar{
            display: block;
        }       
        .displayNone{
            display: none;
        }
    }

}
