@use '../base/mixins' as m;
@use '../base/variables' as v;

.formulario{
    width: 100%;
    margin-top: 3rem;
}

.campo{
    display: flex;
    align-items: center;
    label{
        flex: 0 0 10rem;
    }
    input{
        
        margin: 1rem auto;
        border: none;
        border-radius: 1rem;
        padding: .9rem 2rem;
        width: 95%;
        &:disabled{
            color: v.$blanco;
            background-color: lighten(v.$negro, 12%);
        }
        @include m.telefono {
            width: 85%;
        }
    }
}
