@use '../base/variables' as v;
@use '../base/mixins' as m;

.tabs{

    @include m.tablet{
        display: flex;
    }

    button{
        display: block;
        width: 95%;
        background-color: v.$azul;
        color: v.$blanco;
        padding: 1rem;
        margin: .5rem auto;
        border: none;
        text-transform: uppercase;
        font-weight: v.$bold;
        transition: background-color 300ms ease;
        &:hover{
            cursor: pointer;
            background-color: darken(v.$azul, 10%);
        }
        &.selected{
            background-color: v.$blanco;
            color: v.$azul;
        }
    }
    
}