@use 'variables' as v;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin gridServicios {
    @media (min-width: v.$gridServicios) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto, $display) {
    display: $display;
    background-color: $bgColor;
    color: $colorTexto;
    text-align: center;
    font-size: 2rem;
    font-weight: 700;
    padding: 1.5rem 4rem;
    border: none;
    border-radius: 1rem;
    transition-property: background-color;
    transition-duration: .3s;
    margin: 2rem auto;
    width: 95%;
    @include telefono {
        width: auto;
    }
    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}