html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
  margin-top: 3rem;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

@media (min-width: 1024px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen-app {
  background-image: url(../img/1.jpg);
  background-position: center center;
  background-size: cover;
  height: 25rem;
}
@media (min-width: 1024px) {
  .contenedor-app .imagen-app {
    height: auto;
  }
}
.contenedor-app .app {
  margin: 0 auto;
  padding: 3rem 0;
  width: 95%;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: scroll;
  }
}

.seccion {
  display: none;
  padding: 5rem 0;
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.barra {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.barra span {
  font-weight: 700;
}
.barra .boton {
  display: inline-block;
  background-color: #cb0000;
  color: #FFFFFF;
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
  padding: 1.5rem 4rem;
  border: none;
  border-radius: 1rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  margin: 2rem auto;
  width: 95%;
  width: auto;
  margin: 0;
}
@media (min-width: 480px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: #980000;
  cursor: pointer;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
  width: 95%;
  margin: auto;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicios {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgba(230, 230, 230, 0.927);
  padding: 2rem;
  border-radius: 12px;
  transition-property: transform;
  transition-duration: 300ms;
}
.listado-servicios .servicios:hover {
  transform: scale(1.06);
}
.listado-servicios .servicios.seleccionado {
  background-color: #0da6f3;
  color: #FFFFFF;
}
.listado-servicios .servicios.seleccionado .precio-servicio {
  margin-top: 1rem;
  color: #FFFFFF;
  font-weight: 700;
}
.listado-servicios p {
  color: #1a1b15;
  text-align: center;
  margin: 0;
}
.listado-servicios .precio-servicio {
  margin-top: 1rem;
  color: #0da6f3;
  font-weight: 700;
}

.citas, .servicios {
  list-style: none;
  padding: 0;
}
.citas li, .servicios li {
  border-top: 1px solid #e1e1e1;
}
.citas li p, .servicios li p {
  font-weight: 700;
  color: #0da6f3;
}
.citas li span, .servicios li span {
  font-weight: 400;
  color: #FFFFFF;
}

.tab-servicio {
  background-color: #0da6f3;
  padding: 0 2rem;
  border-radius: 7px;
  width: 100%;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.tab-servicio:hover {
  cursor: pointer;
  background-color: #0a85c3;
}

.service-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.service-title .titulo, .service-title .titulo_flecha {
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2.6rem;
}
.service-title .titulo_flecha {
  transform: rotate(270deg);
}
.service-title .rotate {
  transform: rotate(90deg);
}

.servicios-detaill {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s linear, opacity 0.8s, height 0.1s linear;
  height: 0;
  padding: 0 2rem;
}
.servicios-detaill:last-child {
  border-bottom: 3px dashed #FFFFFF;
}

.visibility {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .barra-servicios {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
}

.back {
