@use '../base/mixins' as m;
@use '../base/variables' as v;

.listado-servicios{

    display: grid;
    gap: 2rem;
    width: 95%;
    margin: auto;

    @include m.tablet {
        @include m.grid(2, 1rem);
    }

    .servicios{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background-color: darken(rgba(255, 255, 255, 0.927), 10%);
        padding: 2rem;
        border-radius: 12px;
        transition-property: transform;
        transition-duration: 300ms;

        &:hover{
            transform: scale(1.06);
        }
        &.seleccionado{
            background-color: v.$azul;
            color: v.$blanco;
            .precio-servicio{
                margin-top: 1rem;
                color: v.$blanco;
                font-weight: v.$bold;
            }
        }
    }
    p{
        color: v.$negro;
        text-align: center;
        margin: 0;
    }
    .precio-servicio{
        margin-top: 1rem;
        color: v.$azul;
        font-weight: v.$bold;
    }
}