@use '../base/variables' as v;
@use '../base/mixins' as m;

.contenedor-app{
    @include m.desktop {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        min-height: 100vh;
    }
    .imagen-app{
        background-image: url(../img/1.jpg);
        background-position: center center;
        background-size: cover;
        height: 25rem;
        @include m.desktop {
            height: auto;
        }
    }
    .app{
        margin: 0 auto;
        padding: 3rem 0;
        width: 95%;
        @include m.tablet {
            overflow:scroll;
        }
    }
}